.detail-content {
  .breadcrumb {
    padding: 10px 0;
  }
  .article {
    width: 100%;
    max-width: 864px;
    padding: 0 20px;
    background-color: #fff;
    border: 1px solid #e8e8e8;
    box-shadow: 0 2px 8px rgba(115, 115, 115, 0.08);

    font-size: 14px;
    color: #262626;
    cursor: text;
  }
  .article-content {
    padding: 20px 20px 90px 20px;
  }
  .page {
    display: flex;
    border-top: 1px solid #ededed;
    .page-item {
      flex: 1;
      padding: 20px;
      cursor: pointer;
      &.next {
        text-align: right;
      }
      .name {
        margin-bottom: 8px;
        font-size: 14px;
        color: #bfbfbf;
      }
      .title {
        font-size: 16px;
        font-weight: 500;
        color: #666;
      }
    }
  }
}

@primary-color: #79a8a9;@link-color: #79a8a9;@border-radius-base: 2px;